<template>
  <div id="main">
    <article class="wrapper">
      <nav class="sidebar">
        <div class="sidebar-box">
          <h2></h2>
          <div v-for="nav in navList" class="subnav">
            <h3>{{nav.title}}</h3>
            <ul>
              <li v-for="list in nav.list">
                <a :href="list.url">{{list.name}}</a>
                <span class="hot" v-show="list.hot">HOT</span>
              </li>
            </ul>
            <div class="hr" v-show="!nav.last"></div>
          </div>
        </div>
        <div class="sidebar-box">
          <h2></h2>
          <ul>
            <li v-for="news in newList">
              <a :href="news.url">{{news.name}}</a>
            </li>
          </ul>
        </div>
      </nav>
      <section class="container">
        <div class="carousel-box">
          <carousel></carousel>
        </div>
        <div class="column-box">
          <column></column>
        </div>
      </section>
    </article>
  </div>
</template>

<script>
import $ from 'jquery'
import carousel from './carousel'
import column from './column'
export default {
  name: 'index',
  components: {
    carousel,
    column
  },
  data () {
    return {
      navList:{
        PC:{
          title:'PC',
          list:[
            {
              name:'',
              url:'http://www.baidu.com',
              hot:true
            },
            {
              name:'',
              url:'http://www.qq.com',
              hot:true
            },
            {
              name:'',
              url:'http://www.alibaba.com'
            },
            {
              name:'',
              url:'http://www.taobao.com'
            }
          ]
        },
        Phone:{
          title:'',
          last:true,
          list:[
            {
              name:'',
              url:'http://www.baidu.com',
            },
            {
              name:'',
              url:'http://www.qq.com',
              hot:true
            },
            {
              name:'',
              url:'http://www.alibaba.com'
            }
          ]
        }
      },
      newList:[
        {
          name:'',
          url:'http://www.baidu.com'
        },
        {
          name:'',
          url:'http://www.qq.com'
        },
        {
          name:'',
          url:'http://www.alibaba.com'
        },
        {
          name:'',
          url:'http://www.taobao.com'
        }
      ]
    }
  }
}
</script>
<style>
.wrapper {
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}
.sidebar {
  float: left;
  width: 300px;
  margin: 15px 15px 15px 0;
}
.sidebar-box {
  margin-bottom: 15px;
  padding-bottom: 15px;
  box-shadow: 0 0 1px #ddd;
  background: #fff;
}
.sidebar-box h2 {
  line-height: 35px;
  background: #4fc08d;
  padding: 0 15px;
  color: #fff;
}
.sidebar-box h3 {
  line-height: 30px;
  font-weight: bold;
  padding: 0 15px;
  margin: 5px 0;
}
.sidebar-box ul {
  margin: 10px 0;
}
.sidebar-box ul li {
  line-height: 25px;
  padding: 0 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.hot {
  background: red;
  color: #fff;
  font-size: 12px;
}
.hr {
  width: 270px;
  height: 1px;
  background: #eee;
  margin: 10px 15px;
}
.container{
  float: right;
  width: 885px;
  margin: 15px 0;
}
.el-button--primary:focus, .el-button--primary:hover {
  color: #fff;
}
</style>



